<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="8"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <li class="active">资料设置</li>
            </ul>
          </div>
          <div class="centerContent">
            <div class="row">
              <label class="title">当前头像</label>
              <div class="txBox" @click="chooseImg">
                <img v-if="imageUrl" class="avatar" :src="imageUrl" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </div>
              <p class="clear"></p>
              <p class="beizhu">*仅支持JPG、GIF、PNG、JPEG、BMP格式 , 文件小于2M</p>
            </div>
            <div class="row clearfix">
              <label class="title">昵称</label>
              <input class="textInp" v-model="username" type="text" placeholder="请输入您的昵称" value="data.username">
            </div>
            <div class="row clearfix">
              <label class="title">真实姓名</label>
              <input class="textInp" v-model="name" type="text" placeholder="请输入您的姓名">
            </div>
            <div class="row clearfix">
              <label class="title">性别</label>
              <div class="sex">
                <template>
                  <el-radio v-model="sex" label="1">男</el-radio>
                  <el-radio v-model="sex" label="0">女</el-radio>
                </template>
              </div>
            </div>
            <div class="row clearfix">
              <label class="title">账号ID</label>
              <p class="desc">{{data.phone}}</p>
            </div>
            <div class="row clearfix">
              <label class="title">注册时间</label>
              <p class="desc">{{data.addtime}}</p>
            </div>
            <div class="keepBtn" @click="getKeep">保存</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from "@/api/data-api"
  export default {
    name: "index.vue",
    data(){
      return{
        data: {},
        imageUrl: '',
        username:'',
        name:'',
        sex: ''
      }
    },
    created(){
      this.getData();
    },
    methods: {
      getData(){
        api.getUserData().then(data => {
          this.data = data
          this.imageUrl = data.avatar;
          this.username = data.username;
          this.name = data.name;
          this.sex = data.sex;
        });
      },

      getKeep:function(){
        const {imageUrl,username,name,sex} = this;
        console.log(imageUrl)
        this.$router.push("/data");
      },
      chooseImg:function(){

      }

    },
    components: {
      PersonalMenu,
      PersonalHeader
    }
  }
</script>

<style lang="less" scoped>
  .centerBg{
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }
  .centerLeft{
    float: left;
    width: 230px;
  }
  .centerRight{
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;
    .centerTitle{
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      ul{
        li{
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
          border-bottom: 2px solid #fff;
        }
        li.active{
          border-bottom: 2px solid #4F74F4;
        }
      }
    }
    .centerContent{
      padding: 35px 0 0;
      .row{
        margin-bottom: 15px;
        .title{
          float: left;
          width: 100px;
          line-height: 40px;
          text-align: right;
          margin-right: 15px;
        }
        .txBox{
          float: left;
          width: 94px;
          height: 94px;
          border: 1px solid #E3E4E5;
          img{
            display: block;
            width: 94px;
            height: 94px;
          }
          i{
            color: #E1E3EB;
          }
        }
        .beizhu{
          font-size: 12px;
          color: #999;
          margin: 15px 0 34px;
          padding-left: 117px;
        }
        .textInp{
          width: 338px;
          height: 38px;
          line-height: 38px;
          border: 1px solid #E3E4E5;
          border-radius:2px;
          text-indent: 15px;
        }
        .sex{
          height: 40px;
          line-height: 40px;
        }
        .desc{
          float: left;
          height: 40px;
          line-height: 40px;
          color: #000;
        }
      }
      .keepBtn{
        width: 120px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #fff;
        background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
        background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
        background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
        background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
        background: linear-gradient(to bottom, #579EFA, #4F74F4);
        -webkit-border-radius: 2px;
        -moz-border-radius: 2px;
        border-radius: 2px;
        margin: 30px 0 0 117px;
      }

    }
  }
  .avatar-uploader{
    float: left;
    width: 94px;
    height: 94px;
    line-height: 94px;
    text-align: center;
    border: 1px solid #E3E4E5;
  }
  .el-upload {
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .el-upload img{
    display: block;
    width: 94px;
    height: 94px;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 94px;
    height: 94px;
    line-height: 94px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



